import React from 'react'
import PropTypes from 'prop-types'
import Modal from 'antd/lib/modal'
import Input from 'antd/lib/input'
import Alert from 'antd/lib/alert'
import './styles.css'

export default class NewMessage extends React.Component {
    static propTypes = {
        onOK: PropTypes.func.isRequired,
        onCancel: PropTypes.func.isRequired,
        visible: PropTypes.bool,
    }

    static defaultProps = {
        visible: false,
    }

    constructor(props) {
        super(props)
        this.state = {
            address: '',
            subject: '',
            content: '',
            tips: '',
            confirmLoading: false,
        }
        this.formItems = [
            {id: 'address', title: '', placeholder: ''},
            {id: 'subject', title: '', placeholder: ''},
            {
                id: 'content',
                title: '',
                placeholder: '100',
                Component: Input.TextArea,
                attrs: {rows: 3},
            },
        ]
    }

    handleChange = (evt, id) => {
        this.setState({
            [id]: evt.target.value,
            tips: '',
        })
    }

    handleOK = () => {
        const {address, subject, content} = this.state
        const tAddress = address.trim()
        const tSubject = subject.trim()
        const tContent = content.trim()

        if (!tAddress) {
            this.setState({tips: ''})
            return
        }
        if (!window.Account.isValidAddress(tAddress)) {
            this.setState({tips: ''})
            return
        }
        if (!tSubject || tSubject.length > 30) {
            this.setState({tips: ',30'})
            return
        }
        if (tContent.length > 100) {
            this.setState({tips: '100'})
            return
        }

        this.setState({confirmLoading: true}, async () => {
            await this.props.onOK({
                address: tAddress,
                subject: tSubject,
                content: tContent,
            })

            this.setState({
                address: '',
                subject: '',
                content: '',
                confirmLoading: false,
            })
        })
    }

    render() {
        const {visible, onCancel} = this.props
        const {tips} = this.state

        return (
            <Modal
                title=""
                visible={visible}
                onOk={this.handleOK}
                onCancel={onCancel}
                okText=""
                cancelText=""
                wrapClassName="NewMessage"
                destroyOnClose
            >
                {
                    this.formItems.map(({id, title, placeholder, Component = Input, attrs = {}}) => (
                        <div className="NewMessage__Row" key={id}>
                            <label htmlFor={id} title={title}>{title}</label>
                            <Component
                                id={id}
                                value={this.state[id]}
                                onChange={evt => this.handleChange(evt, id)}
                                placeholder={placeholder}
                                {...attrs}
                            />
                        </div>
                    ))
                }

                {
                    !!tips
                    && (
                        <div className="NewMessage__Row">
                            <Alert message={tips} type="error" showIcon />
                        </div>
                    )
                }

                <div className="NewMessage__Row">
                    <Alert message="" type="info" showIcon />
                </div>
            </Modal>
        )
    }
}



